<template>
    <el-dialog title="收货地址" :visible.sync="forms.isVisible">
      <el-form :model="form">
        <el-form-item label="活动名称" :label-width="formLabelWidth">
          <el-input v-model="forms.text" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="活动区域" :label-width="formLabelWidth">
          <el-select v-model="form.region" placeholder="请选择活动区域">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="forms.isVisible = false">取 消</el-button>
        <el-button type="primary" @click="forms.isVisible = false">确 定</el-button>
      </div>
    </el-dialog>
</template>
<script>
export default {
  props: {
    formDatas: {
      type: Object,
      default: false
    }
  },
  data() {
    return {
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px",
      forms: {},
      
    };
  },
  watch: {
    formDatas: {
      handler(newVal, oldVal) {
        this.forms = newVal;
      },
      deep: true
    },
    forms: {
      handler(newVal, oldVal) {
        this.$emit('update:formDatas', newVal);
      },
      deep: true
    }
  }
};
</script>
